@font-face {
  font-family: 'portfolioIcons';
  src:  url('./icomoon.eot?fh68z9');
  src:  url('./icomoon.eot?fh68z9#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?fh68z9') format('truetype'),
    url('./icomoon.woff?fh68z9') format('woff'),
    url('./icomoon.svg?fh68z9#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="i-"], [class*=" i-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'portfolioIcons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

