.experiments {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    max-width: 1200px;
    width: 100%;
    margin: auto;
    overflow-x: auto;
}

.experiment {
    cursor: pointer;
    padding: 25px 20px 10px;
    background-color: #131116;
    border-radius: 3px;
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    -webkit-box-shadow: 0 25px 48px rgba(0, 0, 0, 0.5);
            box-shadow: 0 25px 48px rgba(0, 0, 0, 0.5);
    margin: 25px;
}

.experiment:hover {
    -webkit-transform: translateY(-10px) translateZ(0);
            transform: translateY(-10px) translateZ(0);
}

.experiment-image-container {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 15px;
}

.experiment-image {
    width: 250px;
}

.experiment-date {
    font-size: 14px;
    opacity: 0.4;
    display: inline-block;
}

.experiment-number {
    float: right;
}

.experiment-title {
    text-align: center;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 0.8px;
    width: 100%;
    text-transform: uppercase;
}

.experiment-footer {
    position: absolute;
    bottom: 30px;
    left: 31px;
    font-size: 14px;
}

.experimentSketch_shadow {
    border-radius: 4px;
    -webkit-box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.15);
            box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.15);
}

.experimentSketch_padding {
    padding: 25px 25px 21px;
}


