.portfolioDrawer {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgba(33, 47, 62, .25);
    cursor: w-resize;
    -webkit-transition: background-color .5s ease-out;
    -o-transition: background-color .5s ease-out;
    transition: background-color .5s ease-out;
}

.portfolioDrawer-modal {
    cursor: default;
    position: relative;
    right: 0;
    min-height: 100%;
    overflow-y: auto;
    width: 100%;
    max-width: 830px;
    background-color: #FFFFFF;
    -webkit-box-shadow: 5px 0 50px rgba(50, 47, 58, 0.25);
            box-shadow: 5px 0 50px rgba(50, 47, 58, 0.25);
    padding: 70px 50px 30px;
    -webkit-animation-name: drawer;
            animation-name: drawer;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

@-webkit-keyframes drawer {
    from {right: 100vw;}
    to {right: 0;}
}

@keyframes drawer {
    from {right: 100vw;}
    to {right: 0;}
}

.portfolioDrawer-close {
    position: absolute;
    cursor: pointer;
    padding: 10px;
    top: 5px;
    right: 0;
    font-size: 2em;
    opacity: 0.7;
    -webkit-transition: opacity .15s;
    -o-transition: opacity .15s;
    transition: opacity .15s;
}

@media (max-width: 836px) {
    .portfolioDrawer-close {
        position: fixed;
    }
}

.portfolioDrawer-close:hover {
    opacity: 1;
}

.portfolioDrawer-modal h1 {
    margin-top: 0;
}

.portfolioDrawer_willOpen, .portfolioDrawer_closing {
    background-color: rgba(33, 47, 62, 0);
    cursor: default;
}

.portfolioDrawer_willOpen .portfolioDrawer-modal, .portfolioDrawer_closing .portfolioDrawer-modal {
    -webkit-transform: translateX(0) translateZ(0);
            transform: translateX(0) translateZ(0);
}

.portfolioDrawer_opening {
    background-color: rgba(33, 47, 62, .25);
}

.portfolioDrawer_opening .portfolioDrawer-modal {
    -webkit-transform: translateX(100vw) translateZ(0);
            transform: translateX(100vw) translateZ(0);
}

